import "./contact.css";
import Phone from "../../img/Phone.png";
import Email from "../../img/Email.png";
import { useRef, useState } from "react";
import gh from "../../img/gh.png";
import React from 'react';
import emailjs from '@emailjs/browser';
import { ThemeContext } from "../../context";
import { useContext } from "react";


const Contact = () => {
    const formRef = useRef();
    const [done, setDone] = useState(false);
    const theme = useContext(ThemeContext);
    const darkMode = theme.state.darkMode;



    const handleSubmit = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_pkax4dj',
            'template_qx06juw',
            formRef.current,
            ' user_pxdRMjzqcbhHjzyf643Sd')
            .then(
                (result) => {
                    console.log(result.text);
                    setDone(true);
                }, (error) => {
                    console.log(error.text);
                });
    }
    return (
        <div className="c">
            <div className="c-bg"></div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-title">Let's descuss your project</h1>
                    <div className="c-info">
                        <div className="c-info-item">
                            <img
                                src={Phone}
                                alt=""
                                className="c-icon" />+996 502 15 51 89
                        </div>
                        <div className="c-info-item">
                            <img
                                src={Email}
                                alt=""
                                className="c-icon" />janara.satybaldieva.kk@gmail.com
                        </div>
                        <div className="c-info-item">
                            <img
                                src={gh}
                                alt=""
                                className="c-icon" />Janara-S
                        </div>
                    </div>
                </div>
                <div className="c-right">
                    <p className="c-desc">
                        <b>What's your story?</b> Get in touch. Always freelancing if the right project comes along.
                    </p>
                    <form ref={formRef} onSubmit={handleSubmit}>
                        <input style={{ backgroundColor: darkMode && "#333" }} type="text" placeholder="Name" name="user_name" />
                        <input style={{ backgroundColor: darkMode && "#333" }} type="text" placeholder="Subject" name="user_subject" />
                        <input style={{ backgroundColor: darkMode && "#333" }} type="text" placeholder="Email" name="user_email" />
                        <textarea style={{ backgroundColor: darkMode && "#333" }} rows="5" placeholder="Message" name="message" />
                        <button>Submit</button>
                        {done && "Thenk you message"}
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact
